.timedate{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:72px;font-family:'neoneonregular', serif;color:#fff;background-color:#00000099;text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;padding:60px 60px 30px 60px;-webkit-border-radius:20px;-moz-border-radius:20px;border:0.2rem solid #fff;border-radius:20px;box-shadow:0 0 0.5rem #fff,inset 0 0 0.5rem #fff,0 0 2rem var(--neon-border-color),inset 0 0 2rem var(--neon-border-color),0 0 4rem var(--neon-border-color),inset 0 0 4rem var(--neon-border-color)}.timedate .caption{text-align:center;font-family:'Vibur', cursive;font-size:100px;transform:rotate(-12deg);margin-top:-38px}.timedate .full-date{margin-top:60px}.timedate .minutes:before,.timedate .seconds:before,.timedate .milliseconds:before{content:':'}.timedate hr{border:1px;height:3px;background-image:linear-gradient(to right, rgba(255,255,255,0.9), #fff, rgba(255,255,255,0.9));box-shadow:0 0 7px rgba(255,255,255,0.75),0 0 10px #fff,0 0 20px #ff0080,0 0 30px #ff0080,0 0 40px #ff0080,0 0 55px #ff0080,0 0 75px #ff0080}.timedate .time{display:flex;justify-content:flex-start}.timedate .hours,.timedate .minutes,.timedate .seconds,.timedate .milliseconds{font-family:'neoneonregular', serif;color:#fff;text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;font-size:80px}.timedate .milliseconds{font-size:60px}@media (max-width: 810px){.timedate{font-size:40px;text-align:center;width:400px;padding:30px}.timedate .hours,.timedate .minutes,.timedate .seconds{font-size:50px}.timedate .milliseconds{font-size:40px}}@media (max-width: 540px){.timedate{font-size:24px;text-align:center;width:240px}.timedate .caption{font-family:'Vibur', cursive;font-size:50px;transform:rotate(-12deg);margin:0}.timedate .full-date{margin-top:20px}.timedate .hours,.timedate .minutes,.timedate .seconds{font-size:36px}